import React, { useEffect, useState } from 'react';
import './App.css';
import List from './components/List';
import withListLoading from './components/withListLoading';
import axios from "axios";
function App() {
  const ListLoading = withListLoading(List);
  const [appState, setAppState] = useState({
    loading: false,
    repos: null,
    pools: null,
  });

  useEffect(() => {
    setAppState({ loading: true });
        const response = axios.post('https://chain.wax.io/v1/chain/get_table_rows', {
            code: "s.rplanet",
            index_position: 1,
            json: true,
            key_type: "i64",
            limit: 1000,
            lower_bound: "",
            reverse: false,
            scope: "s.rplanet",
            show_payer: false,
            table: "collections",
            upper_bound: ""
        }).then((res) =>  {
            const pools = axios.post('https://chain.wax.io/v1/chain/get_table_rows', {
                code: "s.rplanet",
                index_position: 1,
                json: true,
                key_type: "i64",
                limit: 1000,
                lower_bound: "",
                reverse: false,
                scope: "s.rplanet",
                show_payer: false,
                table: "pools",
                upper_bound: ""
            }) .then((res2) =>  setAppState({ loading: false, repos: res.data.rows, pools: res2.data.rows }))
        });

  }, [setAppState]);
  return (
      <div className='App'>
        <div className='repo-container'>
          <ListLoading isLoading={appState.loading} repos={appState.repos} pools={appState.pools} />
        </div>
        <footer>
          <div className='footer'>
            Built{' '}
            <span role='img' aria-label='love'>
            💚
          </span>{' '}
          </div>
        </footer>
      </div>
  );
}
export default App;
